.inputs__container {
	width: 480px;
	height: auto;
	display: flex;
	flex-direction: row;
	justify-content: space-between;
	align-items: center;
	margin: 64px auto 64px auto;
}
@media (max-width: 768px) {
	.inputs__container {
		flex-direction: column;
		width: 300px;
		height: 180px;
	}
}
